@import '../../sass/main.scss';

.donepage {
    color: $color-grey-dark-2;
    display: flex;
    margin-top: 3rem;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    h1 {
        font-size: 2.4rem;
        margin-bottom: 2rem;

    }

    &__svg {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 1rem;


        &>* {
            height: 100%;
            width: 25rem;
        }
    }

    p {
        font-size: 1.2rem;

    }
}