@import '../../sass/main.scss';

.homepage {
    position: relative;
    color: $color-grey-dark-2;
    padding-left: 2rem;
    height: 100vh;
    align-items: center;

    &__title {
        font-family: Poppins;
        line-height: 1;
        font-size: 2.7rem;
        margin-bottom: -.2rem;
    }

    &__content {
        display: flex;
        margin-top: -1rem;
    }

    &__form {
        height: 100%;
        flex: 0 0 40%;
    }

    &__svg {
        display: flex;
        align-items: center;
        justify-content: center;
        flex: 0 0 60%;
        flex-direction: column;
        margin-top: -4rem;
    }

    &__warning {
        display: flex;
        font-size: 1.3rem;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        position: absolute;
        height: 10rem;
        width: 20rem;
        box-shadow: 0 2rem 2rem rgba($color-black, .3);
        top: 40%;
        left: 50%;
        transform: translate(-50%, -50%);
        background-color: white;
        border-radius: 20px;

        i {
            font-size: 1.5rem;
        }

        button {
            cursor: pointer;
            font-size: 1.1rem;
            color: white;
            font-family: Poppins;
            border: none;
            width: 10rem;
            padding: .3rem .1rem;
            border-radius: 20px;
            background-color: $color-primary-0;

            &:hover {
                background-color: #f76400;
            }
        }
    }
}

.home-svg {
    height: 24rem;
    width: 20rem;
}